/**
 * CompareViewTableAccordion.js
 * Accordion view for table in compare view
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import React, { Component } from 'react';
import { Collapse, CardBody, Card } from 'reactstrap';
import PropTypes from 'prop-types';

import Arrow from 'assets/images/forms/arrow.svg';

class CompareViewTableAccordion extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isOpened: false,
      height: 0,
    };

    this.recursiveUpdate = this.recursiveUpdate.bind(this);
    this.handleResize = this.handleResize.bind(this);
  }

  componentDidMount() {
    window.addEventListener('resize', this.handleResize);
    this.handleResize();
  }

  /* shouldComponentUpdate(nextProps, nextState) {
    return nextState !== this.state;
  } */

  componentWillUnmount() {
    window.removeEventListener('resize', this.handleResize);
  }

  handleResize() {
    this.setState({
      height: this.accordion.children[2].children[0].offsetHeight,
    });
  }

  recursiveUpdate(level, max) {
    if (level < max) {
      const { updateTabHeight } = this.props;
      setTimeout(() => { updateTabHeight(); this.recursiveUpdate(level + 1, max); }, 200);
    }
  }

  render() {
    const { isOpened, height } = this.state;
    const {
      bgStyle,
      title,
      description,
      updateTabHeight,
      firstStyle,
      lastStyle,
    } = this.props;
    const rotateStyle = isOpened ? '180deg' : '0deg';

    return (
      <div className="info-accordian-container" ref={node => this.accordion = node}>
        <div
          role="button"
          tabIndex={-1}
          onKeyDown={() => {}}
          className={`desc-accordion-container ${bgStyle} ${firstStyle} ${lastStyle}`}
          onClick={() => {
            this.setState({ isOpened: !isOpened });
            if (!isOpened) {
              updateTabHeight(height);
            } else {
              updateTabHeight(-height);
            }
          }}
        >
          <p className="title">{title}</p>
          <p className="desc">{description}</p>
          <div className="arrow-container">
            <img src={Arrow} alt="Arrow.svg" style={{ transform: `rotate(${rotateStyle})` }} />
          </div>
        </div>
        <Collapse isOpen={isOpened}>
          <Card>
            <CardBody>
              {this.props.children}
            </CardBody>
          </Card>
        </Collapse>
        <div className="temp-container">
          <Collapse isOpen={true}>
            <Card>
              <CardBody>
                {this.props.children}
              </CardBody>
            </Card>
          </Collapse>
        </div>
      </div>
    );
  }
}

CompareViewTableAccordion.defaultProps = {
  updateTabHeight: null,
  firstStyle: '',
  lastStyle: '',
};

CompareViewTableAccordion.propTypes = {
  bgStyle: PropTypes.string.isRequired,
  title: PropTypes.string.isRequired,
  description: PropTypes.string.isRequired,
  updateTabHeight: PropTypes.func,
  firstStyle: PropTypes.string,
  lastStyle: PropTypes.string,
};


export default CompareViewTableAccordion;
