/**
 * StepProgressBar.js
 * Used to display the progress steps used in all categories
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import { withRouter } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import { Row, Col } from 'reactstrap';

import Slider from '../../Forms/Slider/Slider';

import { setInsuranceFormState, FORMSTATES } from '../../../actions/insuranceActions';

import './StepProgressBar.scss';

class StepProgressBar extends Component {
  constructor(props) {
    super(props);
    this.getStateList = this.getStateList.bind(this);
    this.renderDescription = this.renderDescription.bind(this);
  }

  getStateList() {
    const {
      stateList,
      history,
    } = this.props;

    return _.map(stateList, (state, index) => ({
      state: state.index,
      callback: (formState) => {
        const currIndex = _.findIndex(stateList, s => s.index === formState);
        history.go(-(currIndex - index));
      },
    }));
  }

  renderDescription() {
    const {
      formState,
      stateList,
      stateDescriptions,
      charged,
      history,
    } = this.props;

    try {
      const currIndex = _.findIndex(stateList, state => state.index === formState);
      const mainDesc = charged ? 'Receipt' : stateDescriptions[currIndex].main;
      const subDesc = stateDescriptions[currIndex].sub;

      if (!mainDesc && !subDesc) {
        return null;
      }

      return (
        <Col className="step-progress-description-container" xs={12}>
          {
            currIndex !== 0 ?
              <button
                className="back-button"
                onClick={() => {
                  history.goBack();
                }}
              >
                Back
              </button>
            :
              null
          }
          {
            mainDesc ?
              <p className="main">{mainDesc}</p>
            :
              null
          }
          {
            subDesc ?
              <p className="sub">{subDesc}</p>
            :
              null
          }
        </Col>
      );
    } catch (error) {
      return null;
    }
  }

  render() {
    const {
      formState,
      charged,
      inputList,
      maxWidth,
    } = this.props;

    if (formState !== FORMSTATES.MAIN.index && !charged) {
      return (
        <Row noGutters>
          <Col className="d-none d-sm-flex mx-auto product-slider" xs={12}>
            <Slider
              isProductSlider
              hideWidth={576}
              maxWidth={maxWidth}
              inputList={inputList}
              formStateList={this.getStateList()}
              formState={formState}
            />
          </Col>
          {this.renderDescription()}
        </Row>
      );
    }

    return null;
  }
}

StepProgressBar.defaultProps = {
  charged: null,
  maxWidth: '400px',
};

StepProgressBar.propTypes = {
  formState: PropTypes.number.isRequired,
  // setInsurFormState: PropTypes.func.isRequired,
  inputList: PropTypes.array.isRequired,
  stateList: PropTypes.array.isRequired,
  charged: PropTypes.bool,
  stateDescriptions: PropTypes.array.isRequired,
  maxWidth: PropTypes.string,
  // form: PropTypes.string.isRequired,
  history: PropTypes.object.isRequired,
};

const mapDispatchToProps = dispatch => ({
  setInsurFormState: (state) => {
    dispatch(setInsuranceFormState(state));
  },
});

export default withRouter(connect(null, mapDispatchToProps)(StepProgressBar));
