/**
 * CompareView.js
 *
 * - The Compare view
 * - 2 modes, products changable or just fixed comparaison.
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';
// import EditButton from 'assets/images/forms/EditButton.svg';
import Logos from 'assets/images/InsurerLogos';
import TabView from '../TabView/TabView';
import Accordion from '../Accordion/Accordion';
import Button from '../../FormsV3/Button/Button';
import CompareViewTable from './CompareViewTable';
import SpeechScroll from '../SpeechScroll/SpeechScroll';
import RestrictionsPopup from '../RestrictionsPopup/RestrictionsPopup';

import { setHoveredInsurerIndex } from '../../../actions/insuranceActions';
import { setRestrictionPopup } from '../../../actions/restrictionsActions';
import { ampTag } from '../../../lib/tagFunctions';
import './CompareView.scss';
import '../../Motor/MotorCommon.scss';

class CompareView extends Component {
  static getDerivedStateFromProps(nextProps, prevState) {
    const { data, currentTab } = nextProps;
    if (data !== prevState.data || currentTab !== prevState.currentTab) {
      return ({
        data,
        currentTab,
      });
    }

    return null;
  }

  constructor(props) {
    super(props);

    const { data, currentTab/* , forceFixedOnDesktop */ } = this.props;
    // isFixed - Used to determine sticky headers in responsive view
    // categoryWidth - Used to determine sticky headers in responsive view
    this.state = {
      isFixed: false, // forceFixedOnDesktop,
      categoryWidth: `${100 / 3}%`,
      data,
      currentTab,
    };

    this.handleScroll = this.handleScroll.bind(this);
    this.handleOnClick = this.handleOnClick.bind(this);

    this.renderTable = this.renderTable.bind(this);
    this.renderLists = this.renderLists.bind(this);

    this.renderHeader = this.renderHeader.bind(this);
    this.renderEditForm = this.renderEditForm.bind(this);
    this.renderEditableHeader = this.renderEditableHeader.bind(this);
    this.renderBottomHeader = this.renderBottomHeader.bind(this);

    this.renderSpeechScrollList = this.renderSpeechScrollList.bind(this);
    this.renderNonProductChangable = this.renderNonProductChangable.bind(this);
    this.renderProductChangable = this.renderProductChangable.bind(this);
  }

  componentDidMount() {
    // window.addEventListener('scroll', this.handleScroll);
    window.scrollTo(0, 0);

    if (window.$('#intercom-container')) {
      window.$('#intercom-container').hide();
    }
  }

  componentWillUnmount() {
    if (window.$('#intercom-container')) {
      window.$('#intercom-container').show();
    }

    // window.removeEventListener('scroll', this.handleScroll);
  }

  handleScroll() {
    const { isFixed } = this.state;
    const { forceFixedOnDesktop } = this.props;
    if (!forceFixedOnDesktop && window.innerWidth > 576) {
      if (isFixed) {
        this.setState({
          isFixed: false,
        });
      }
      return;
    }

    if (this.catMarker) {
      const scrollPos = this.catMarker.getBoundingClientRect().top;

      let isNowFixed = false;

      if (this.bottomHeader) {
        const markerPosForBottom = window.innerHeight - this.bottomHeader.offsetHeight;
        isNowFixed = scrollPos > markerPosForBottom;
      } else {
        isNowFixed = scrollPos <= 60;
      }

      // console.log(scrollPos);
      if (isNowFixed !== isFixed) {
        this.setState({
          isFixed: isNowFixed,
        });
      }
    }
  }

  handleOnClick(selectedInsurer, clickCallback, doTag) {
    const { category } = this.props;
    if (doTag) {
      doTag();
    }

    if (selectedInsurer.restrictions) {
      ampTag(`${_.startCase(category)}RestrictionsS`);
      this.props.setRestrictionPopup({
        values: selectedInsurer.restrictions,
        clickCallback,
        doAcceptTag: () => ampTag(`${_.startCase(category)}AcceptA`),
      });
      return;
    }

    clickCallback();
  }

  renderHeader() {
    const {
      categoryWidth,
      data,
      currentTab,
    } = this.state;

    const { buttonDesc, category } = this.props;

    const currPlanData = data.plans[currentTab];
    if (!data.policyTypes || !currPlanData || !currPlanData.premium) {
      return null;
    }

    let isPriceUsed = false;
    return _.reduce(data.policyTypes, (result, type, index) => {
      const leftStyle = index !== 0 ? 'border-left' : '';
      const premiumData = currPlanData.premium[index];

      if (!premiumData) {
        result.push(<div key={`header-${index}`} className={`category-container ${leftStyle}`} style={{ width: categoryWidth }} />);
      }

      const isPrice = premiumData.price && premiumData.price !== premiumData.net_price;
      const price = isPrice ? `$${premiumData.price.toFixed(2)}` : '';

      isPriceUsed = isPriceUsed || isPrice;

      const buttonText = buttonDesc || (premiumData.net_price ? 'Buy Now' : 'Enquire');

      const selectedInsurer = 'default' in data.plans ? data.selectedInsurers[0] : data.selectedInsurers[index];

      const cashbackProduct = premiumData.cashback && premiumData.cashback > 0 ? `Earn ${(premiumData.cashback * premiumData.net_price).toFixed(2)} cashback` : null;

      let cashbackText = null;
      if (category === 'life' || category === 'endowment' || category === 'retirement') {
        cashbackText = 'Get $5 Cashback!';
      } else if (category === 'travel' || category === 'motor' || category === 'pa' || category === 'pet') {
        cashbackText = 'Get 5% Cashback!';
      }

      const cashbackJSX = cashbackProduct ? (
        <p className="cashback">{cashbackProduct}</p>
      ) : (
        <a
          className="cashback cashback-link"
          href="/faq"
          target="__blank"
        >
          {cashbackText}
        </a>
      );


      const renderData = (
        <div key={`header-${index}`} className={`category-container top-header ${leftStyle}`} style={{ width: categoryWidth }}>
          <p className="category">{_.startCase(type)}</p>
          <p className="sub-category">{premiumData.frequency}</p>
          <p className="cost">{`$${premiumData.net_price.toFixed(2)}`}</p>
          {
            isPriceUsed ?
              <p className="bottom-discount">{price}</p>
            :
              null
          }
          <div className="buy-button">
            <Button
              variant="medium-flat-green"
              width={165}
              height={48}
              onClick={() => this.handleOnClick(selectedInsurer, premiumData.onClick, premiumData.doTag)}
            >
              <p>{buttonText}</p>
            </Button>
            {
              cashbackProduct || cashbackText ?
                cashbackJSX
              :
                <p className="cashback" />
            }
          </div>
        </div>
      );

      result.push(renderData);
      return result;
    }, []);
  }

  renderEditForm() {
    const { editForm } = this.props;
    if (editForm) {
      return (
        <div className="desc-width overview edit-form">
          {editForm}
        </div>
      );
    }

    return <div className="desc-width overview d-none d-sm-flex" />;
  }

  renderEditableHeader() {
    const {
      categoryWidth,
      speechScroll,
      data,
    } = this.state;

    const { setHoveredIndex, hoveredIndex } = this.props;

    if (!speechScroll) {
      return null;
    }

    let policyType = 'default';
    if (data.tripType) {
      policyType = data.tripType === 'Single Trip' ? 'single' : 'annual';
    }
    const editableHeaders = _.map(data.selectedInsurers, (insurer) => {
      if (!insurer) {
        return null;
      }

      const matchedInsurer = _.find(data.validInsurers, insur => insur.insurer_slug === insurer.insurerSlug);
      let discountData = null;
      try {
        const discountRawData = _.find(data.selectedInsurers, i => i.insurerSlug === insurer.insurerSlug);
        if (discountRawData) {
          discountData = discountRawData.discount[policyType];
        }
      } catch (error) {
        // eslint-disable-next-line
      }
      // const discountData = matchedInsurer.discount_data ? matchedInsurer.discount_data[policyType] : null;
      const discount = discountData && discountData.amount > 0 ? discountData.display : null;
      // try {
      //   discount = discount.toFixed(2);
      // } catch (error) {
      //   console.log('Discount to fixed failed!');
      // }
      const discountText = discount ? `UP TO ${discount} OFF` : null;

      return ({
        insurerSlug: matchedInsurer.insurer_slug,
        insurer: matchedInsurer.insurer_name,
        product: matchedInsurer.insurance_name,
        discount: discountText,
      });
    });

    return _.map(editableHeaders, (header, index) => {
      const inurerLogoName = header ? `${header.insurer}Logo` : `empty-${index}`;
      const noDiscountPadding = header && header.discount ? '' : 'no-discount';

      return (
        <div
          key={inurerLogoName}
          className={`category-container product-changable-category ${noDiscountPadding}`}
          style={{ width: categoryWidth }}
        >
          <button
            className="edit-container"
            tabIndex={-1}
            onClick={() => {

            }}
            onMouseMove={(event) => {
              if (hoveredIndex !== index) {
                setHoveredIndex(index);
              }
              speechScroll.handleInsurerHover(event);
            }}
          >
            More Options
          </button>
          {
            header ?
              <Fragment>
                <div className="insurer-container">
                  <img src={Logos[header.insurerSlug]} alt={`${inurerLogoName}.png`} />
                </div>
                <p className="product-text">{header.product}</p>
                {
                  header.discount ?
                    <div className="discount">
                      <p>{header.discount}</p>
                    </div>
                  :
                    null
                }
              </Fragment>
            :
             null
          }
        </div>
      );
    });
  }

  renderBottomHeader() {
    const {
      categoryWidth,
      data,
      currentTab,
    } = this.state;

    const {
      productChangable,
      buttonDesc,
      category,
    } = this.props;

    const currPlanData = data.plans[currentTab];
    if (!currPlanData) {
      return null;
    }
    const { premium } = currPlanData;
    const isPriceExists = _.reduce(
      premium, (result, header) => result || (header && header.net_price && (header.net_price !== header.price)),
      false,
    );

    const headerList = _.map(premium, (header, index) => {
      const leftStyle = productChangable ? 'line-border' : '';

      if (!header) {
        return (
          <div
            key={`cashback${index}`}
            className={`category-container bottom-border na ${leftStyle}`}
            style={{ width: categoryWidth }}
          >
            <p className="na-text">N/A</p>
          </div>
        );
      }

      const netPrice = header.net_price ? `$${header.net_price.toFixed(2)}` : '';
      const price = header.price ? header.price.toFixed(2) : '';

      const discountPrice = netPrice && netPrice.toString() !== `$${price.toString()}` ? `$${price}` : '';
      const buttonText = buttonDesc || (header.net_price ? 'Buy Now' : 'Enquire');

      const cashbackProduct = header.cashback && header.net_price ? `Earn ${(header.cashback * header.net_price).toFixed(2)} cashback` : null;
      let cashbackText = null;
      if (category === 'life' || category === 'endowment' || category === 'retirement') {
        cashbackText = 'Get $5 Cashback!';
      } else if (category === 'travel' || category === 'motor' || category === 'pa' || category === 'pet') {
        cashbackText = 'Get 5% Cashback!';
      }

      const cashbackJSX = cashbackProduct ? (
        <p className="cashback">{cashbackProduct}</p>
      ) : (
        <a
          className="cashback cashback-link"
          href="/faq"
          target="__blank"
        >
          {cashbackText}
        </a>
      );

      return (
        <div
          key={`cashback${index}`}
          className={`category-container bottom-border ${leftStyle}`}
          style={{ width: categoryWidth }}
        >
          {
            netPrice !== '' ?
              <p className="bottom-cost">{netPrice}</p>
            :
              null
          }
          {
            isPriceExists ?
              <p className="bottom-discount">{discountPrice}</p>
            :
              null
          }
          {
            header.life_frequency ?
              <p className="extra-info">{header.life_frequency}</p>
            :
              null
          }
          <div className="buy-button">
            <Button
              variant="medium-flat-green"
              width={180}
              height={48}
              onClick={() => this.handleOnClick(data.selectedInsurers[index], header.onClick, header.doTag)}
            >
              <p>{buttonText}</p>
            </Button>
            {
              cashbackText || cashbackProduct ?
                cashbackJSX
              :
                null
            }
          </div>
        </div>
      );
    });

    return (
      <div className="info-container">
        <div className="category-container-bottom">
          <div className="desc-width bottom-overview bottom-border d-none d-md-flex" />
          <div className="category-resp-container">
            {headerList}
          </div>
        </div>
      </div>
    );
  }

  renderLists(isMultiple) {
    const { categoryWidth, data, currentTab } = this.state;

    const {
      productChangable,
    } = this.props;

    const currPlanData = data.plans[currentTab];
    if (!currPlanData) {
      return null;
    }

    if (isMultiple) {
      return _.map(data.coverageTypes, (type, index) => (
        <CompareViewTable
          data={currPlanData.coverages[type]}
          disclaimer={currPlanData.coverages.disclaimer}
          productChangable={productChangable}
          width={categoryWidth}
          key={`${type}-${index}`}
        />
      ));
    }
    return (
      <CompareViewTable
        data={currPlanData.coverages.default}
        disclaimer={currPlanData.coverages.disclaimer}
        productChangable={productChangable}
        width={categoryWidth}
      />
    );
  }

  renderTable() {
    const { data } = this.state;
    const {
      notAccordion,
    } = this.props;

    const tabs = _.map(data.coverageTypesDescs, tab => ({ desc: tab }));

    if (tabs.length > 1) {
      return (
        <TabView
          style={{ marginTop: 44, marginBottom: 34 }}
          tabs={tabs}
          autoHeight
          setTriggerHeightUpdate={() => {
            if (!notAccordion) {
              this.accordion.setOpenCallback();
            }
          }}
          name="overview-tab"
        >
          {this.renderLists(true)}
        </TabView>
      );
    }

    return this.renderLists(false);
  }

  renderSpeechScrollList() {
    const { speechScroll, data } = this.state;
    const { scrollPopUpCallback, hoveredIndex } = this.props;
    if (!speechScroll) {
      return null;
    }

    const validForSelection = _.reduce(data.validInsurers, (result, insurer) => {
      const matchedCurrentIndex = _.findIndex(data.selectedInsurers, insur => insur && insur.productSlug === insurer.insurance_slug);
      if (matchedCurrentIndex === -1) {
        result.push(insurer);
      }
      return result;
    }, []);

    if (validForSelection.length === 0) {
      return (
        <button>
          <p style={{ textAlign: 'center', flex: 1 }}>None</p>
        </button>
      );
    }

    let policyType = 'default';
    if (data.tripType) {
      policyType = data.tripType === 'Single Trip' ? 'single' : 'annual';
    }
    return _.map(validForSelection, (insurer, index) => {
      const discountData = insurer.discount_data ? insurer.discount_data[policyType] : null;
      const discount = discountData && discountData.amount > 0 ? discountData.display : null;

      return (
        <button
          key={`data.insurer${index}`}
          onClick={(event) => {
            if (scrollPopUpCallback) scrollPopUpCallback(insurer, hoveredIndex);
            speechScroll.handleInsurerBlur(event);
          }}
        >
          <div className="insurer-container">
            <img src={Logos[insurer.insurer_slug]} alt={`${insurer.insurer_name}.png`} />
          </div>
          <p>{insurer.insurer_name}</p>
          {
            discount ?
              <div className="popup-discount">
                <p className="up">UP TO</p>
                <div className="bottom">
                  <p className="bottom-left">{discount.slice(0, discount.length - 1)}</p>
                  <div className="bottom-right">
                    <p className="percent">%</p>
                    <p className="off">OFF</p>
                  </div>
                </div>
              </div>
            :
            null
          }
        </button>
      );
    });
  }

  renderNonProductChangable() {
    const {
      isFixed,
    } = this.state;

    const { notAccordion } = this.props;

    const fixedCatStyle = isFixed ? 'category-resp-fixed-container' : 'category-resp-fixed-hidden-container';
    const headerList = this.renderHeader();

    return (
      <Col className="overview-container" xs={12}>
        <Row className="detail-container" noGutters>
          <div className="desc-width overview d-none d-md-flex">
            <p className="overview-text">Overview</p>
          </div>
          <div ref={node => (this.catMarker = node)} />
          <div className={`category-resp-container ${fixedCatStyle}`}>
            {headerList}
          </div>
          <div className="category-resp-container">
            {headerList}
          </div>
          {
            notAccordion ?
              <Fragment>
                {this.renderTable()}
              </Fragment>
            :
              <Accordion ref={node => this.accordion = node}>
                {this.renderTable()}
              </Accordion>
          }
        </Row>
      </Col>
    );
  }

  renderProductChangable() {
    const {
      isFixed,
    } = this.state;

    const { notAccordion, className } = this.props;

    const fixedCatStyle = isFixed ? 'category-resp-fixed-bottom-container' : 'category-resp-fixed-hidden-container';
    const headerList = this.renderBottomHeader();

    return (
      <Col className={`overview-container  ${className}`} xs={12}>
        <Row className="detail-not-changable-container" noGutters>
          {this.renderEditForm()}
          <div className="category-resp-container">
            <SpeechScroll
              offsetHeight={40}
              ref={node => !this.state.speechScroll && this.setState({ speechScroll: node })}
            >
              {this.renderSpeechScrollList()}
            </SpeechScroll>
            {this.renderEditableHeader()}
          </div>
          {
            notAccordion ?
              <Fragment>
                {this.renderTable()}
                <div className="category-resp-container" ref={node => this.bottomHeader = node}>
                  {headerList}
                  <div ref={node => (this.catMarker = node)} />
                </div>
              </Fragment>
            :
              <Accordion ref={node => this.accordion = node}>
                {this.renderTable()}
                <div className="category-resp-container" ref={node => this.bottomHeader = node}>
                  {headerList}
                  <div ref={node => (this.catMarker = node)} />
                </div>
              </Accordion>
          }
          <div className={`category-resp-container ${fixedCatStyle}`}>
            {headerList}
          </div>
        </Row>
      </Col>
    );
  }

  render() {
    const { productChangable } = this.props;
    return (
      <Fragment>
        { productChangable ?
          this.renderProductChangable()
          :
          this.renderNonProductChangable()
        }
        <RestrictionsPopup ref={node => this.resPopup = node} />
      </Fragment>
    );
  }
}

CompareView.defaultProps = {
  notAccordion: false,
  productChangable: false,
  className: '',
  editForm: null,
  scrollPopUpCallback: null,
  currentTab: null,
  hoveredIndex: 0,
  buttonDesc: null,
  forceFixedOnDesktop: false,
  category: null,
};

CompareView.propTypes = {
  data: PropTypes.object.isRequired,
  notAccordion: PropTypes.bool,
  productChangable: PropTypes.bool,
  className: PropTypes.string,
  editForm: PropTypes.object,
  currentTab: PropTypes.string,
  hoveredIndex: PropTypes.number,
  scrollPopUpCallback: PropTypes.func,
  setHoveredIndex: PropTypes.func.isRequired,
  setRestrictionPopup: PropTypes.func.isRequired,
  buttonDesc: PropTypes.string,
  forceFixedOnDesktop: PropTypes.bool,
  category: PropTypes.string,
};

const mapStateToProps = state => ({
  hoveredIndex: state.insurance.hoveredIndex,
});

const mapDispatchToProps = dispatch => ({
  setHoveredIndex: (index) => {
    dispatch(setHoveredInsurerIndex(index));
  },
  setRestrictionPopup: (data) => {
    dispatch(setRestrictionPopup(data));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(CompareView);
