/**
 * CompareViewTable.js
 * Renders the table view in compare view.
 *
 * @version 0.0.1
 * @author [Wilson](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Tick from 'assets/images/common/Tick.svg';
import Cross from 'assets/images/forms/cross.svg';

import CompareViewTableAccordion from './CompareViewTableAccordion';
import './CompareView.scss';

class CompareViewTable extends Component {
  // Recursive function to handle normal rows or dropdown rows
  // If dropdown, it will recursively render the contents in the dropdown using this
  // same function
  renderTable(data, productChangable, width, updateTabHeight, disclaimer = null) {
    const itemBorderStyle = productChangable ? 'item-border' : '';
    const infoContainerStyle = productChangable ? 'info-container-border' : '';

    const isValidDisclaimer = disclaimer && _.reduce(disclaimer.targets, (result, target) => (
      target || result
    ), false);
    const arrayToMap = isValidDisclaimer ? _.union(data, [disclaimer]) : data;

    return _.map(arrayToMap, (content, idx) => {
      const bgStyle = idx % 2 ? 'white' : 'green';

      if (content.dropdown) {
        return (
          <CompareViewTableAccordion
            key={`${content.title}${idx}`}
            bgStyle={bgStyle}
            title={content.title}
            description={content.description}
            updateTabHeight={updateTabHeight}
          >
            {this.renderTable(content.subContent, productChangable, width)}
          </CompareViewTableAccordion>
        );
      }

      const subTitle = content.sub_title ? <p className="desc title-desc">{_.startCase(content.sub_title)}</p> : null;
      return (
        <div className="info-container" key={`${content.title}${idx}`}>
          {
            content.title ?
              <div className={`desc-resp-container d-flex d-md-none ${bgStyle} ${infoContainerStyle}`}>
                <p className="title">{content.title}</p>
                {subTitle}
              </div>
            :
              null
          }
          <div className={`item-container ${bgStyle} ${infoContainerStyle}`}>
            <div className="d-none d-md-flex item-desc desc-width">
              <p className="title">{content.title}</p>
              {subTitle}
            </div>
            <div className="category-resp-container">
              {
                _.map(content.targets, (target, index) => {
                  if (!target) {
                    return (
                      <div
                        key={`${target}${index}`}
                        className={`item ${itemBorderStyle}`}
                        style={{ width }}
                      >
                        <div className="item-inner">
                          N/A
                        </div>
                      </div>
                    );
                  }

                  const isDisclaimer = isValidDisclaimer && idx === arrayToMap.length - 1;
                  const paddingStyle = isDisclaimer ? 'no-padding' : '';

                  let description = null;
                  if (target.description === 'YES') {
                    description = <img className="tick" src={Tick} alt="Tick.svg" />;
                  } else if (target.description === 'NO') {
                    description = <img className="tick" src={Cross} alt="cross.svg" />;
                  }

                  return (
                    <div
                      key={`${target.value}${index}`}
                      className={`item ${itemBorderStyle} ${paddingStyle}`}
                      style={{ width }}
                    >
                      <div className="item-inner">
                        {
                          target.value === 'Covered' ?
                            <img className="tick" src={Tick} alt="Tick.svg" />
                          :
                          target.value
                        }
                        {
                          description || <p className="desc">{target.description}</p>
                        }
                      </div>
                    </div>
                  );
                })
              }
            </div>
          </div>
        </div>
      );
    });
  }

  render() {
    const {
      data,
      disclaimer,
      productChangable,
      width,
      updateTabHeight,
    } = this.props;

    if (!data) {
      return null;
    }

    return this.renderTable(data.content, productChangable, width, updateTabHeight, disclaimer);
  }
}

CompareViewTable.defaultProps = {
  disclaimer: null,
  updateTabHeight: null,
};

CompareViewTable.propTypes = {
  data: PropTypes.object.isRequired,
  productChangable: PropTypes.bool.isRequired,
  width: PropTypes.string.isRequired,
  disclaimer: PropTypes.object,
  updateTabHeight: PropTypes.func,
};


export default CompareViewTable;
