/**
 * DateInner.js
 * DateView only for rendering
 *
 * @version 0.0.1
 * @author [Wilson Phoo](wilson@policypal.com)
 */
import _ from 'lodash';
import React, { Component, Fragment } from 'react';
import {
  Dropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap';

import CloseIcon from 'assets/images/common/CloseIcon.svg';
import Arrow from 'assets/images/forms/arrow.svg';
import Triangle from 'assets/images/forms/triangle.svg';

import './DateInner.scss';

// Day Text for display
const dayTextList = [
  'SU',
  'MO',
  'TU',
  'WE',
  'TH',
  'FR',
  'SA',
];
// Month text for display
const monthTextMapping = [
  'January',
  'Feburary',
  'March',
  'April',
  'May',
  'June',
  'July',
  'August',
  'September',
  'October',
  'November',
  'December',
];

// Default year range if not user defined
const deafultYearRange = 100;
// Height for month and year dropdown items
const monthItemHeight = 32;
const yearItemHeight = 32;

/**
 * Custom Form Dropdown class
 */
class DateInner extends Component {
  constructor(props) {
    super(props);
    // Get current date
    const date = new Date();
    const year = date.getFullYear();
    const month = date.getMonth();
    // Get first and last day of the month to display
    const firstDay = new Date(year, month, 1);
    const lastDay = new Date(year, month + 1, 0);
    // Get the calender range
    const dateRangeStart = new Date(year - deafultYearRange, month);
    const dateRangeEnd = new Date(year + deafultYearRange, month);

    // isOpened - Whether dropdown is opened
    // currDate - Current selected date in view
    // currFirstDay - Current first day of the month in view
    // currLastDay - Current last day of the month in view
    // monthDropdownOpen - Whether month selector is opened
    // yearDropdownOpen - Whether year selector is opened
    // selectedStartDate - Selected start date(If single select also use this)
    // selectedEndDate - Selected end date for date range picker
    // dateRangeStart - The actual calender start range
    // dateRangeEnd - The actual calender end range
    // isArrowUp - The arrow image of datepicker
    // setSelectedDate - Function to set selected date from main control
    // setNewDate - Function to set new date from main control
    this.state = {
      isOpened: false,
      currDate: date,
      currFirstDay: firstDay,
      currLastDay: lastDay,
      monthDropdownOpen: false,
      yearDropdownOpen: false,
      selectedStartDate: null,
      selectedEndDate: null,
      dateRangeStart,
      dateRangeEnd,
      isArrowUp: true,
      setSelectedDate: null,
      setNewDate: null,
    };

    this.setCalender = this.setCalender.bind(this);
    this.setArrowUp = this.setArrowUp.bind(this);
    this.setNewState = this.setNewState.bind(this);
    this.toggleDateInner = this.toggleDateInner.bind(this);
    this.toggleMonth = this.toggleMonth.bind(this);
    this.toggleYear = this.toggleYear.bind(this);
  }

  /**
   * Lifecycle Method - componentDidUpdate
   *
   * Used to update dropdown position
   * of month and year selector
   */
  componentDidUpdate() {
    // Ref to month and year dropdown
    const {
      yearInput,
      monthInput,
    } = this;

    const {
      currDate,
      dateRangeStart,
    } = this.state;

    // Get current year and month
    const year = currDate.getFullYear();
    const month = currDate.getMonth();

    // Update year scroll position to current year
    if (yearInput) {
      const height = year - dateRangeStart.getFullYear();
      const yearInputScroll = yearInput.childNodes[0];
      yearInputScroll.scrollTop = yearItemHeight * height;
    }

    // Update year scroll position to current month
    if (monthInput) {
      const monthInputScroll = monthInput.childNodes[0];
      monthInputScroll.scrollTop = monthItemHeight * month;
    }

    if (this.dateMonthInput) {
      this.dateMonthInput.childNodes[0].setAttribute('tabindex', '-1');
    }

    if (this.dateYearInput) {
      this.dateYearInput.childNodes[0].setAttribute('tabindex', '-1');
    }
  }

  /**
   * setCalender
   *
   * Set visible calender
   *
   * @param {number} month
   * @param {number} year
   */
  setCalender(month, year) {
    const {
      dateRangeStart,
      dateRangeEnd,
      setNewDate,
    } = this.state;

    let date = new Date(year, month);

    // Cap date range
    if (date < dateRangeStart) {
      date = dateRangeStart;
    } else if (date > dateRangeEnd) {
      date = dateRangeEnd;
    }

    setNewDate(date);
    // this.wrapperRef.focus();
  }

  setNewState(data) {
    this.setState({ ...data });
  }

  setArrowUp(isArrowUp) {
    this.setState({ isArrowUp });
  }

  toggleDateInner(isOpen, settings) {
    this.setState({ isOpened: isOpen });
    if (isOpen) {
      this.setState({ ...settings });
    }
  }

  /**
   * toggleMonth
   *
   * Toggle dropdown
   */
  toggleMonth() {
    const {
      monthDropdownOpen,
    } = this.state;

    this.setState({
      monthDropdownOpen: !monthDropdownOpen,
    });

    this.isFromFocus = false;
  }

  /**
   * toggleYear
   *
   * Toggle dropdown
   */
  toggleYear() {
    const {
      yearDropdownOpen,
    } = this.state;

    this.setState({
      yearDropdownOpen: !yearDropdownOpen,
    });

    this.isFromFocus = false;
  }

  /**
   * renderDaysText
   *
   * Render the days text
   */
  renderDaysText() {
    return _.map(dayTextList, day => (
      <div
        className="date-col"
        key={day}
      >
        <p>{day}</p>
      </div>
    ));
  }

  /**
   * renderDaysSingle
   *
   * Render calender days for single selection
   */
  renderDaysSingle() {
    const {
      currFirstDay,
      currLastDay,
      selectedStartDate,
      dateRangeStart,
      dateRangeEnd,
      setSelectedDate,
    } = this.state;

    const month = currFirstDay.getMonth();
    const year = currFirstDay.getFullYear();

    // Get the starting blank days of the calender
    const startIndex = currFirstDay.getDay();
    // Get the end day
    const endIndex = currLastDay.getDate();

    // Get starting time
    const startTime = selectedStartDate ? selectedStartDate.getTime() : null;

    const result = [];

    // These are blank elements to simulate last month dates
    for (let i = 0; i < startIndex; ++i) {
      result.push(<div className="date-col" key={`blank${i.toString()}`} />);
    }

    for (let i = 1; i <= endIndex; ++i) {
      const currDay = new Date(year, month, i);
      const currTime = currDay.getTime();

      // Check if current day is previously selected day
      const isDayActive = startTime === currTime;
      // Set to active if selected
      const dayStyle = isDayActive ? 'day-active' : 'day-inactive';
      // Block days that are out of range
      const isBlocked = currDay < dateRangeStart || currDay > dateRangeEnd;

      result.push((
        <button
          className="date-col day-container"
          key={`day${i.toString()}`}
          onClick={() => {
            if (!isBlocked) {
              setSelectedDate(new Date(year, month, i));
              this.setState({ isOpened: false });
            }
          }}
          tabIndex={-1}
        >
          <div className="day-range left-inactive" />
          <div className="day-range right-inactive" />
          <div className="end-inactive" />
          {
            isBlocked ?
              <div className="day">
                <p className="blocked">{i}</p>
              </div>
            :
              <div className={`day ${dayStyle}`}>
                <p>{i}</p>
              </div>
          }
        </button>));
    }

    return result;
  }

  /**
   * renderDays
   *
   * Render calender days for range selection
   */
  renderDays() {
    const {
      currFirstDay,
      currLastDay,
      selectedStartDate,
      selectedEndDate,
      dateRangeStart,
      dateRangeEnd,
      setSelectedDate,
      isRange,
      isStart,
    } = this.state;

    const currFirstDayTime = currFirstDay.getTime();
    const currLastDayTime = currLastDay.getTime();

    const month = currFirstDay.getMonth();
    const year = currFirstDay.getFullYear();

    // Get the starting blank days of the calender
    const startIndex = currFirstDay.getDay();
    // Get the end day
    const endIndex = currLastDay.getDate();

    // Get starting time
    const startTime = selectedStartDate ? selectedStartDate.getTime() : null;
    const endTime = selectedEndDate ? selectedEndDate.getTime() : null;

    const result = [];

    // These are blank elements to simulate last month dates
    for (let i = 0; i < startIndex; ++i) {
      result.push(<div className="date-col" key={`blank${i.toString()}`} />);
    }

    for (let i = 1; i <= endIndex; ++i) {
      const currDay = new Date(year, month, i);
      const currDayOfWeek = currDay.getDay();
      const currTime = currDay.getTime();

      const isSat = currDayOfWeek === 6;
      const isSun = currDayOfWeek === 0;
      const isStartDate = startTime === currTime;
      const isEndDate = endTime === currTime;
      const isWithin = startTime && endTime && startTime < currTime && currTime < endTime;
      const isFirstDay = currFirstDayTime === currTime;
      const isLastDay = currLastDayTime === currTime;

      const isSameDay = startTime === endTime;
      const isLeftBlockActive = !isSameDay && (!isSun && !isFirstDay) && ((isEndDate && startTime)
                                || isWithin);
      const isRightBlockActive = !isSameDay && (!isSat && !isLastDay) && ((isStartDate && endTime)
                                || isWithin);
      const isDayActive = isStartDate || isEndDate;
      const isEndActive = isDayActive || (isWithin && (isSat || isSun || isFirstDay || isLastDay));

      // Left bock, right block, circle block and selected circle block
      const leftBlockStyle = isLeftBlockActive ? 'left-active' : 'left-inactive';
      const rightBlockStyle = isRightBlockActive ? 'right-active' : 'right-inactive';
      const endBlockStyle = isEndActive ? 'end-active' : 'end-inactive';
      const dayStyle = isDayActive ? 'day-active' : 'day-inactive';

      // Check if day is in within selected range
      let isBlocked = false;
      if (isRange) {
        if (isStart && selectedEndDate) {
          // isBlocked = currDay > selectedEndDate;
        } else if (!isStart && selectedStartDate) {
          isBlocked = currDay < selectedStartDate;
        }
      }

      // Check if day is out of calender range
      if (!isBlocked) {
        isBlocked = currDay < dateRangeStart || currDay > dateRangeEnd;
      }

      result.push((
        <button
          className="date-col day-container"
          key={`day${i.toString()}`}
          onClick={() => {
            if (!isBlocked) {
              setSelectedDate(new Date(year, month, i));
              this.setState({ isOpened: false });
            }
          }}
          tabIndex={-1}
        >
          <div className={`day-range  ${leftBlockStyle}`} />
          <div className={`day-range  ${rightBlockStyle}`} />
          <div className={`${endBlockStyle}`} />
          {
            isBlocked ?
              <div className="day-disabled">
                <p className="blocked">{i}</p>
              </div>
            :
              <div className={`day ${dayStyle}`}>
                <p>{i}</p>
              </div>
          }
        </button>));
    }

    return result;
  }

  /**
   * renderMonths
   *
   * Render months dropdown
   */
  renderMonths() {
    const {
      currDate,
      dateRangeStart,
      dateRangeEnd,
    } = this.state;

    const startYear = dateRangeStart.getFullYear();
    const endYear = dateRangeEnd.getFullYear();
    const currYear = currDate.getFullYear();

    const startMonth = currYear === startYear ? dateRangeStart.getMonth() : 0;
    const endMonth = currYear === endYear ? dateRangeEnd.getMonth() : 11;

    return _.reduce(monthTextMapping, (result, item, index) => {
      if (startMonth <= index && index <= endMonth) {
        const jsx = (
          <DropdownItem
            onClick={() => {
              this.setCalender(index, currYear);
            }}
            key={`month${index.toString()}`}
          >
            {item}
          </DropdownItem>
        );
        result.push(jsx);
      }

      return result;
    }, []);
  }

  /**
   * renderYears
   *
   * Render years dropdown
   */
  renderYears() {
    const {
      currDate,
      dateRangeStart,
      dateRangeEnd,
    } = this.state;

    const start = dateRangeStart.getFullYear();
    const end = dateRangeEnd.getFullYear();

    const result = [];
    for (let i = start; i <= end; ++i) {
      result.push((
        <DropdownItem
          onClick={() => {
            this.setCalender(currDate.getMonth(), i);
          }}
          key={`year${i.toString()}`}
        >
          {i}
        </DropdownItem>
      ));
    }

    return result;
  }

  /**
   * renderDateInner
   *
   * Render the popup od the datepicker
   */
  renderDateInner() {
    const {
      monthDropdownOpen,
      yearDropdownOpen,
      currDate,
      isRange,
      setSelectedDate,
      selectedStartDate,
      selectedEndDate,
      isStart,
    } = this.state;

    const month = currDate.getMonth();
    const monthText = monthTextMapping[month];
    const year = currDate.getFullYear();

    return (
      <div className="date-inner-container">
        <div
          role="button"
          className="close-button-container"
          onKeyPress={() => {}}
          onClick={() => {
            setSelectedDate(isStart ? selectedStartDate : selectedEndDate);
            this.setState({ isOpened: false });
          }}
          tabIndex={-1}
        >
          <img src={CloseIcon} className="close-icon" alt="close" />
        </div>
        <div className="row" style={{ marginBottom: 30 }}>
          <div
            className="date-arrow-style col-1"
            onClick={() => this.setCalender(month - 1, year)}
            onKeyUp={() => {}}
            role="button"
            tabIndex={-1}
          >
            <img
              src={Arrow}
              alt="arrow.svg"
              className="angle-left"
            />
          </div>
          <div className="col-6 selector-left">
            <Dropdown isOpen={monthDropdownOpen} toggle={this.toggleMonth}>
              <div ref={(dateMonthInput) => { this.dateMonthInput = dateMonthInput; }}>
                <DropdownToggle caret outline>
                  {monthText}
                </DropdownToggle>
              </div>
              <div ref={(input) => { this.monthInput = input; }}>
                <DropdownMenu>
                  {this.renderMonths()}
                </DropdownMenu>
              </div>
            </Dropdown>
          </div>
          <div className="col-4 selector-right">
            <Dropdown isOpen={yearDropdownOpen} toggle={this.toggleYear}>
              <div ref={(dateYearInput) => { this.dateYearInput = dateYearInput; }}>
                <DropdownToggle caret outline>
                  {year}
                </DropdownToggle>
              </div>
              <div ref={(input) => { this.yearInput = input; }}>
                <DropdownMenu>
                  {this.renderYears()}
                </DropdownMenu>
              </div>
            </Dropdown>
          </div>
          <div
            className="date-arrow-style col-1"
            onClick={() => this.setCalender(month + 1, year)}
            onKeyUp={() => {}}
            role="button"
            tabIndex={-1}
          >
            <img
              src={Arrow}
              alt="arrow.svg"
              className="angle-right"
            />
          </div>
        </div>
        <div className="row date-inner-padding">
          {this.renderDaysText()}
        </div>
        <div className="row date-divider" />
        <div className="row date-inner-padding">
          { isRange ? this.renderDays() : this.renderDaysSingle() }
        </div>
      </div>
    );
  }

  render() {
    const {
      isArrowUp,
      isOpened,
    } = this.state;

    const openedStyle = isOpened ? 'dateinner-opened' : '';
    const dateOpenStyle = isOpened ? 'date-container-opened' : 'date-container-closed';

    const arrowStyle = isArrowUp ? 'arrow-up' : 'arrow-down';
    return (
      <Fragment>
        <div className={`${openedStyle} dateinner`}>
          <div
            className={`
                        ${dateOpenStyle} 
                      `}
          >
            <img className={arrowStyle} src={Triangle} alt="triangle.svg" />
            <div
              className="list-container list-opened"
              ref={node => this.listCont = node}
            >
              {
                isOpened ?
                this.renderDateInner()
                :
                null
              }
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

export default DateInner;
